@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    min-height: 100vh;
    background: #cb997e;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Architects Daughter', cursive;
}

img{
    width: 100%;
}

.viewer-container {
    height: 40vh;
    width: 70vw;
    margin-top: 1.5rem;
    box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.5);
    background: #ffe8d6;
}

#editor{
    width: 100%;
    height: 85%;
    resize: none;
    outline: none;
    padding: 0;
    margin: 0;
    border: none;
    background: #ffe8d6;
}

.nav{
    height: 8%;
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 1rem;
    background: #ddbea9;
    align-items: center;
}

.nav i{
    cursor: pointer;
}

.nav i:hover{
    color: #cb997e;
}

.title{
    font-weight: bold;
    letter-spacing: 3px;
}

.preViewer-container{
    height: 40vh;
    width: 70vw;
    margin-top: 1.5rem;
    box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.5);
    background: #ffe8d6;
    margin-bottom: 3rem;
}

#preview{
    height: 85%;
    overflow-y: scroll;
}




